<template>
    <div id="app" class="container-fluid">

        <main class="row">
            <aside class="sidebar col-12 col-lg-2">


                <header class="top-header mt-3">
                    <a href="#">
                        <div class="logo">
                            <div class="chrome-logo"></div>
                        </div>
                        <h1>{{ logo }}</h1>
                    </a>
                </header>
                <div class="main_menu row">

                    <ul class="setting__list">
                        <li class="setting__item"><a href="">{{home}}</a></li>
                        <li class="setting__item"><a href="">{{settings}}</a></li>
                    </ul>
                </div>
            </aside>
            <section class="content col-12 col-lg-10">
                <div class="section_header">
                    <div class="section_header__search">
                        <form class="form" @submit.prevent="addItem">
                            <div class="input-group  col-12  mt-3 mb-3 justify-content-end">
                                <input type="text" class="form-control col-12 col-lg-4 text-left" placeholder="  " id="itemForm">
                                <div class="input-group-append">
                                    <button type="button" class="btn btn-outline-secondary" v-on:click="addItem"></button>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div class="section_header__profile"></div>
                </div>
                <div class="tasks">

                    <div class="tasks__table">
                        <div class="tasks__menu">
                            <div class="tasks__counter"></div>
                            <div class="tasks__view"></div>
                        </div>
                        <div class="tasks__todo">
                            <table class="table table-striped task">
                                <thead class="task__head">
                                    <tr>
                                        <th scope="col">ToDo</th>
                                    </tr>
                                </thead>
                                <tbody class="task__body">
                                <tr v-for="(todo,index) in todos" class="row">
                                    
                                        <td scope="row" class="task__text col-6 col-lg-10  text-left">{{todo.text}}</td>
                                        <td scope="row" class="task__del col-6 col-lg-2"><button type="button" v-on:click="deleteItem(index)" class="btn btn-danger"></button></td>
                                    
                                </tr>
                                </tbody>
                            </table>

                        </div>
                        <div class="tasks__complete">
                            <div class="task">
                                <div class="task__body">
                                    <div class="task__item"></div>
                                    <div class="task__item"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>



        </main>
    </div>


</template>

<script>
    export default {
        name: 'app',
        data () {
            return {
                title: 'your personal ToDo list',
                logo: 'ToDo',
                home: 'Home',
                settings: 'Settings',
                todos: [{text: "First"},{text: "Second"}]
            }
        },
        methods: {
            addItem: function() {
                var input = document.getElementById('itemForm');
                if (input.value !=='') {
                    this.todos.push({text:input.value})
                }
                input.value = "";
            },
            deleteItem: function(index) {
                this.todos.splice(index, 1)
            }
        }
    }
</script>

<style lang="scss">
    body {
        margin: 0;
    }
    a {
        text-decoration: none;
        color: #333;
    }
    a:hover {
        text-decoration: none;
    }
    $color: #50432c;
    #app  {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: $color;
    }
    main {
        height: 100vh;
    }
    .sidebar {
        background-color: #bbb;
    }
    div {
        box-sizing: border-box;
    }
    .top-header {
        height: 80px;
    }
    .top-header a{
        display: flex;
        justify-content: center;
    }
    .main_menu {
        height: calc(100% - 80px);
    }
    /*logo start*/
    .logo {
        position: relative;
        transform: scale(.1);
        height: 50px;
        width: 50px;
        display: inline-block;
        margin-right: 20px;
    }
    // Variables
$logo-size: 450px;
    $base-font: 16px;

    // Mixins
    @mixin center($offset) {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -#{$offset};
        margin-left: -#{$offset};
    }

    // Functions
    @function rem-calc($target, $context: $base-font){
    @return ($target/$context)+0em;
    }


    .chrome-logo{
        display: block;
        width: rem-calc($logo-size);
        height: rem-calc($logo-size);
    @include center(rem-calc($logo-size / 2));
        border-radius: rem-calc($logo-size);
        background-image: linear-gradient(240deg,  transparent 66.4%,  #009C59 0%, #009C59),
        linear-gradient(0deg,    transparent 71.5%,    #E34A35 0%, #E34A35),
        linear-gradient(290deg,  transparent 60%,    #E34A35 0%, #E34A35),
        linear-gradient(120deg,  transparent 66%,  #FFCA38 0%, #FFCA38),
        linear-gradient(57deg,   transparent 58%,    #FFCA38 0%, #FFCA38),
        linear-gradient(190deg,  transparent 58%,    #009C59 0%, #009C59);
        box-shadow: inset 0 0 rem-calc(160px) rgba(0, 0, 0, 0.05),
        0 0 100px rgba(0, 0, 0, 0.05),
        0 0 10px rgba(255, 255, 255, 1);
        transition: all 1s cubic-bezier(0.4, 0, 0.2, 1);
        overflow: hidden;

    &::before,
    &::after {
         content:'';
         display: block;
     }

    &::before{
         width: rem-calc(28px);
         height: rem-calc(38px);
         position: absolute;
         top: rem-calc(423px);
         left: rem-calc(215px);
         border-bottom-right-radius: rem-calc($logo-size / 2);
         background-color: #FFCA38;
         transform: rotate(30deg);
     }

    &::after{
         width: rem-calc(170px);
         height: rem-calc(170px);
     @include center(rem-calc(85px));
         border-radius: rem-calc($logo-size / 2);
         background-color: #228EF4;
         box-shadow: 0 0 0 rem-calc(15px) rgba(255, 255, 255, 1);
     }

    &:hover {
         transform: rotate(-720deg) scale(0.9);
     }
    }
    /*logo end*/
    .setting__list {
        width: 100%;
        margin: 0;
        padding: 0;
        list-style: none;
        text-align: left;
    }
    .setting__item a {
        width: 100%;
        line-height: 30px;
        display: block;
        padding: 10px 20px;
    }
    .setting__item a:hover {
        opacity: .5;
        background-color: blue;
    }

    @media only screen and (max-width:992px) {
        .setting__item {
            display: inline-block;
        }
        .main_menu {
            height:50px;
        }
        .sidebar {
            height: 150px;
        }
    }
</style>



